$primary-color: #1e90ff;
$secondary-color: #ffe221;
$tertiary-color: #ffffff;
.home-page {
	// margin-top: 0;
}
.home {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	.definition {
		width: 100%;
	}
	* {
		transition: none;
	}
	.definition dl {
		max-width: 600px;
	}
	.definition dt {
		opacity: 0;
		overflow-y: hidden;
		animation: stagger-in 1s 1s ease forwards;
	}
	.definition dt .bullet {
		transform: scale(0);
		animation: definition-appear 0.3333333333s 2.5s cubic-bezier(0.26, 0.15, 0.43, 1.42) forwards;
	}
	.definition dt span:nth-child(3) {
		position: absolute;
		transform: translate(0px);
		animation: definition-slide-here 0.5s 2.2s ease forwards;
	}
	.definition dt .pronunciation {
		transform: translate(-75%);
		animation: definition-slide 1s 1.5s ease forwards;
	}
	.definition dd {
		opacity: 0;
		animation: stagger-in 1s 2s ease forwards;
	}
	.definition dd:nth-child(2) {
		animation: stagger-in 0.5s 2.8s ease forwards;
	}
	.definition dd:nth-child(3) {
		animation: stagger-in 0.5s 3.8s ease forwards;
	}
	.definition dd:nth-child(4) {
		animation: stagger-in 0.5s 4.8s ease forwards;
	}

	@keyframes definition-slide-here {
		0% {
			transform: translate(0px);
			-moz-transform: translate(0px);
		}
		100% {
			transform: translate(15px);
			-moz-transform: translate(15px);
		}
	}
	@keyframes definition-slide {
		0% {
			transform: translate(-75%);
		}
		100% {
			transform: translate(0);
		}
	}
	@keyframes definition-appear {
		0% {
			transform: scale(0);
		}
		100% {
			transform: scale(1);
		}
	}
	// .section {
	// 	margin: 4rem 0 !important;
	// }
}

body.menu-open {
	overflow: hidden;
}

@media (max-width: 48em) {
	body.menu-open .site-header {
		background-color: transparent !important;
	}

	body.menu-open a.logo path {
		fill: #fff !important;
	}

	body.menu-open .main-navigation {
		-webkit-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
		-webkit-transition-delay: 0s;
		transition-delay: 0s;
	}

	body.menu-open .main-nav-menu a {
		color: #fff !important;
	}

	body.menu-open .main-nav-menu a:after {
		background-color: #fff !important;
	}

	body.menu-open .menu-slice {
		background-color: #fff !important;
	}

	body.menu-open .menu-slice-1 {
		-webkit-transform: translate3d(0, 8px, 0) rotate(-135deg) !important;
		transform: translate3d(0, 8px, 0) rotate(-135deg) !important;
	}

	body.menu-open .menu-slice-2 {
		opacity: 0 !important;
	}

	body.menu-open .menu-slice-3 {
		-webkit-transform: translate3d(0, -8px, 0) rotate(135deg) !important;
		transform: translate3d(0, -8px, 0) rotate(135deg) !important;
	}
}

.link-wrap {
	display: inline-block;
	position: relative;
}

.callout-link {
	font-size: 0.8em;
	font-weight: 400;
	letter-spacing: 3px;
	text-transform: uppercase;
}

.callout-link:hover:after {
	width: 100%;
}

.callout-link:after {
	position: absolute;
	bottom: 0;
	left: 0;
	height: 1px;
	width: 0;
	background-color: #161616;
	content: '';
	-webkit-transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
	transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.callout-link.white {
	color: #fff;
}

.callout-link.white:after {
	background-color: #fff;
}

.loader {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	height: 32px;
	width: 32px;
	color: #fff;
	display: block;
	font-size: 0;
	margin: auto;
	z-index: 0;
}

.loader:before,
.loader:after {
	position: absolute;
	top: 0;
	left: 0;
	height: 32px;
	width: 32px;
	-webkit-animation: ball-scale-pulse 2s infinite ease-in-out;
	animation: ball-scale-pulse 2s infinite ease-in-out;
	background-color: #fff;
	border: 0 solid #fff;
	border-radius: 100%;
	content: '';
	display: inline-block;
	opacity: 0.5;
}

.loader:after {
	-webkit-animation-delay: -1s;
	animation-delay: -1s;
}

.loader.load-dark {
	color: #000;
}

.loader.load-dark:before,
.loader.load-dark:after {
	background-color: #000;
	border-color: #000;
}

@-webkit-keyframes ball-scale-pulse {
	0%,
	100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}

	50% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}

@keyframes ball-scale-pulse {
	0%,
	100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}

	50% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}

.loader-overlay {
	height: 100%;
	width: 100%;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #161616;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	margin: auto;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	z-index: 9000;
}

.page-loader {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	height: 32px;
	width: 32px;
	opacity: 0;
	visibility: hidden;
	color: #000;
	display: block;
	font-size: 0;
	margin: auto;
	z-index: -1;
}

.page-loader:before,
.page-loader:after {
	position: absolute;
	top: 0;
	left: 0;
	height: 32px;
	width: 32px;
	-webkit-animation: ball-scale-pulse 2s infinite ease-in-out;
	animation: ball-scale-pulse 2s infinite ease-in-out;
	background-color: #000;
	border: 0 solid #000;
	border-radius: 100%;
	content: '';
	display: inline-block;
	opacity: 0.5;
}

.page-loader:after {
	-webkit-animation-delay: -1s;
	animation-delay: -1s;
}

img.lazy {
	opacity: 0;
	-webkit-transform: translate3d(0, 30px, 0);
	transform: translate3d(0, 30px, 0);
	-webkit-transition: opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1),
		-webkit-transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	transition: opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1),
		-webkit-transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	transition: opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1), transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	transition: opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1), transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1),
		-webkit-transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
}

img.lazy.scroll-watch-in-view {
	opacity: 1;
	visibility: visible;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

